import React from 'react'
import Link from 'gatsby-link'
import Rellax from 'rellax'

class IndexPage extends React.Component {
  constructor() {
    super()
    this.state = {}
  }
  render() {
    return (
      <div>
        <h1>Hi Vira :)</h1>

        <section style={{padding: '4rem', background: '#eee'}} ref={ref => { this.rellaxSection = ref }}>
          <h3>
            This is the demo I mentioned on UpWork. It is built using <a href="http://reactjs.org">React</a>, <a href="http://gatsbyjs.org">Gatsby</a> and <a href="https://dixonandmoe.com/rellax/">Rellax</a>.
          </h3>
        </section>

        <h1 ref={ref => { this.rellaxVira = ref }} style={{color: '#e2e2e2', fontSize: '10rem'}}>Vira Karimova</h1>
        <h1 style={{fontSize: '5rem'}} ref={ref => { this.rellaxSmile = ref }}>:)</h1>

        <h2 ref={ref => { this.rellaxRellax = ref }}>Rellax is soft, easy to implement and fast</h2>


      </div>
    )
  }
  componentDidMount() {
    const vira = new Rellax(this.rellaxVira, {
      speed: 8,
    })
    const section = new Rellax(this.rellaxSection, {
      speed: -4
    })
    const rellax = new Rellax(this.rellaxRellax, {
      speed: -4
    })
    const smile = new Rellax(this.rellaxSmile, {
      speed: 8
    })
  }
}

export default IndexPage



